import { Component, OnInit } from '@angular/core';
import {
  BrowserStorageService,
  EStorageKeys,
} from '../../../helpers/browser-storage.service';
import { CardLinkComponent } from '../../../shared/components/card-link/card-link.component';
import { CardLink, DashboardCardLink } from '../../../models/card-link.model';
import { FormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { ChatBoxComponent } from '../../../shared/components/chat-box/chat-box.component';

@Component({
  selector: 'app-home',
  standalone: true,
  imports: [CardLinkComponent, CommonModule, ChatBoxComponent],
  templateUrl: './home.component.html',
  styleUrl: './home.component.scss',
})
export class HomeComponent implements OnInit {
  user = '';
  cardlinks: CardLink[] = DashboardCardLink;

  constructor(private browserStorageService: BrowserStorageService) {}

  ngOnInit(): void {
    const userString = this.browserStorageService.getFromLocalStorage<string>(
      EStorageKeys.USER
    );

    if (userString) {
      const userInfo = JSON.parse(userString);
      this.user = userInfo.first_name + ' ' + userInfo.last_name;
    }
  }
}
