import { Component, Input } from '@angular/core';
import { CardLink } from '../../../models/card-link.model';
import { Router } from '@angular/router';
import { CommonModule } from '@angular/common';
import { Store } from '@ngrx/store';
import { SidenavActions } from '../../../features/components/sidenav/store/sidenav.actions';
import { selectSelectedMenu } from '../../../features/components/sidenav/store/sidenav.selectors';

@Component({
  selector: 'app-card-link',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './card-link.component.html',
  styleUrl: './card-link.component.scss',
})
export class CardLinkComponent {
  selectedMenu$ = this.store.select(selectSelectedMenu);

  @Input() cardlink: CardLink = {
    title: '',
    icon: '',
    route: '',
    parentRoute: '',
  };

  constructor(private router: Router, private store: Store) {}

  selectMenu(menu: string) {
    this.store.dispatch(
      SidenavActions['[Sidenav]SelectMenu']({
        selectedMenu: menu,
      })
    );
  }

  onClick() {
    if (this.cardlink.route) {
      // If there's a parent route, prepend it to the child route
      if (this.cardlink.parentRoute) {
        this.router.navigate([
          `/${this.cardlink.parentRoute}/${this.cardlink.route}`,
        ]);
      } else {
        this.router.navigate([`/${this.cardlink.route}`]); // For absolute routes
      }
      this.selectMenu(this.cardlink.route);

      this.selectedMenu$.subscribe((x) => {
        console.log('cl ', x);
      });
    } else {
      console.log('No route defined for this link');
    }
  }
}
