import { createReducer, on } from '@ngrx/store';
import { SidenavActions } from './sidenav.actions';
export const sidenavFeatureKey = 'sidenav';

export interface SidenavState {
  isOpen: boolean;
  selectedMenu: string;
}

// Initial state
export const initialState: SidenavState = {
  isOpen: false,
  selectedMenu: '',
};

// Create the reducer
export const sidenavReducer = createReducer(
  initialState,
  on(SidenavActions['[Sidenav]Open'], (state) => ({ ...state, isOpen: true })),
  on(SidenavActions['[Sidenav]Close'], (state) => ({
    ...state,
    isOpen: false,
  })),
  on(SidenavActions['[Sidenav]Toggle'], (state) => ({
    ...state,
    isOpen: !state.isOpen,
  })),
  on(SidenavActions['[Sidenav]SelectMenu'], (state, { selectedMenu }) => ({
    ...state,
    selectedMenu,
  }))
);
