import { createFeatureSelector, createSelector } from '@ngrx/store';
import { SidenavState } from './sidenav.reducer';

export const selectSidenavState =
  createFeatureSelector<SidenavState>('sidenav');

export const selectIsSidenavOpen = createSelector(
  selectSidenavState,
  (state: SidenavState) => state.isOpen
);

export const selectSelectedMenu = createSelector(
  selectSidenavState,
  (state: SidenavState) => state.selectedMenu
);
