import { Routes } from '@angular/router';
import { path } from './models/routes.model';
import { AuthGuard } from './core/auth/guard/auth.guard';
import { LayoutComponent } from './core/layout/layout.component';
import { HomeComponent } from './features/pages/home/home.component';
import { ManageEmployeesComponent } from './features/pages/manage-employess/manage-employess.component';
import { QuickOrderComponent } from './features/pages/quick-order/quick-order.component';
import { TemplatesComponent } from './features/pages/manage-employess/components/templates/templates.component';
import { EmployeeGroupComponent } from './features/pages/manage-employess/components/employee-group/employee-group.component';
import { EmployeeComponent } from './features/pages/manage-employess/components/employee/employee.component';
import { ProjectsComponent } from './features/pages/manage-employess/components/projects/projects.component';
import { BulkEditComponent } from './features/pages/manage-employess/components/bulk-edit/bulk-edit.component';
import { TransactionAdjustmentComponent } from './features/pages/manage-employess/components/transaction-adjustment/transaction-adjustment.component';
import { MyAccountComponent } from './features/pages/my-account/my-account.component';
import { DeliveryAddressesComponent } from './features/pages/my-account/delivery-addresses/delivery-addresses.component';
import { ChangePasswordComponent } from './features/pages/my-account/change-password/change-password.component';
import { EmployeeLoginsComponent } from './features/pages/my-account/employee-logins/employee-logins.component';
import { DefaultPermissionsComponent } from './features/pages/my-account/default-permissions/default-permissions.component';
import { PurchaseOrdersComponent } from './features/pages/my-account/purchase-orders/purchase-orders.component';

export const routes: Routes = [
  {
    path: '',
    loadChildren: () => import('./core/auth/auth.routes').then((m) => m.routes),
  },
  {
    path: '',
    component: LayoutComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: path.HOME,
        component: HomeComponent,
        data: { breadcrumb: 'Home' },
      },
      {
        path: path.QUICK_ORDER,
        component: QuickOrderComponent,
        data: { breadcrumb: 'Quick Order' },
      },
      {
        path: path.MANAGE_EMPLOYEE,
        component: ManageEmployeesComponent,
        data: { breadcrumb: 'Manage Employee' },
        children: [
          {
            path: path.TEMPLATES,
            component: TemplatesComponent,
            data: { breadcrumb: 'Add/Edit Templates' },
          },
          {
            path: path.EMPLOYEE_GROUP,
            component: EmployeeGroupComponent,
            data: { breadcrumb: 'Add/Edit Employee Group' },
          },
          {
            path: path.EMPLOYEE,
            component: EmployeeComponent,
            data: { breadcrumb: 'Add/Edit Employee' },
          },
          {
            path: path.PROJECTS,
            component: ProjectsComponent,
            data: { breadcrumb: 'Add/Edit Project' },
          },
          {
            path: path.BULK_EDIT,
            component: BulkEditComponent,
            data: { breadcrumb: 'Bulk Edit' },
          },
          {
            path: path.TRANSANCTION_ADJUSTMENT,
            component: TransactionAdjustmentComponent,
            data: { breadcrumb: 'Make Transaction Adjustments' },
          },
        ],
      },
      {
        path: path.MY_ACCOUNT,
        component: MyAccountComponent,
        data: { breadcrumb: 'My Account' },
        children: [
          {
            path: path.DELIVERY_ADDRESSES,
            component: DeliveryAddressesComponent,
            data: { breadcrumb: 'Delivery Addresses' },
          },
          {
            path: path.CHANGE_PASSWORD,
            component: ChangePasswordComponent,
            data: { breadcrumb: 'Change Password' },
          },
          {
            path: path.EMPLOYEE_LOGIN,
            component: EmployeeLoginsComponent,
            data: { breadcrumb: 'Employee Logins' },
          },
          {
            path: path.DEFAULT_PERMISSION,
            component: DefaultPermissionsComponent,
            data: { breadcrumb: 'Default Permissions' },
          },
          {
            path: path.PURCHASE_ORDERS,
            component: PurchaseOrdersComponent,
            data: { breadcrumb: 'Purchase Orders' },
          },
        ],
      },
      {
        path: '**',
        redirectTo: path.HOME, // Redirect to home or a 404 page
      },
    ],
  },
];
