import { Component, OnDestroy, OnInit } from '@angular/core';
import { CardLinkComponent } from '../../../shared/components/card-link/card-link.component';
import { CommonModule } from '@angular/common';
import { CardLink, ManageEmployeeLink } from '../../../models/card-link.model';
import { NavigationEnd, Router, RouterModule } from '@angular/router';
import { filter, Subject, takeUntil } from 'rxjs';
import { ChatBoxComponent } from '../../../shared/components/chat-box/chat-box.component';

@Component({
  selector: 'app-manage-employess',
  standalone: true,
  imports: [CardLinkComponent, CommonModule, RouterModule, ChatBoxComponent],
  templateUrl: './manage-employess.component.html',
  styleUrl: './manage-employess.component.scss',
})
export class ManageEmployeesComponent implements OnInit, OnDestroy {
  cardlinks: CardLink[] = ManageEmployeeLink;
  isManageEmployeeDashboard = false;
  private readonly destroy$ = new Subject<void>();

  constructor(private router: Router) {}

  ngOnInit(): void {
    this.updateDashboardState(this.router.url);
    this.handleRouteChanges();
  }

  private handleRouteChanges(): void {
    this.router.events
      .pipe(
        filter((event) => event instanceof NavigationEnd),
        takeUntil(this.destroy$)
      )
      .subscribe((event: NavigationEnd) => {
        this.updateDashboardState(event.urlAfterRedirects);
      });
  }

  private updateDashboardState(url: string): void {
    this.isManageEmployeeDashboard = url === '/manage-employee';
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
