import { Component, HostListener } from '@angular/core';
import { SidenavComponent } from '../../features/components/sidenav/sidenav.component';
import { NavbarComponent } from '../../features/components/navbar/navbar.component';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { MainComponent } from '../../features/components/main/main.component';
import { Store } from '@ngrx/store';
import {
  selectIsSidenavOpen,
  selectSelectedMenu,
} from '../../features/components/sidenav/store/sidenav.selectors';

@Component({
  selector: 'app-layout',
  standalone: true,
  imports: [
    CommonModule,
    RouterModule,
    SidenavComponent,
    NavbarComponent,
    MainComponent,
  ],
  templateUrl: './layout.component.html',
  styleUrl: './layout.component.scss',
})
export class LayoutComponent {
  isOpen$ = this.store.select(selectIsSidenavOpen);
  selectedMenu$ = this.store.select(selectSelectedMenu);
  isOpen = false; // Track if the sidenav is open
  screenWidth: number = window.innerWidth;

  constructor(private store: Store) {}

  // Listen to screen resize
  @HostListener('window:resize', ['$event'])
  onResize(event: Event): void {
    this.screenWidth = window.innerWidth;
  }

  toggleSidenav(): void {
    this.isOpen = !this.isOpen;
  }
}
