import { CommonModule } from '@angular/common';
import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { MENUS } from '../../../models/sidebar-menu.model';
import { Router } from '@angular/router';
import { AuthService } from '../../../core/auth/services/auth/auth.service';
import { path } from '../../../models/routes.model';
import { Store } from '@ngrx/store';
import {
  selectIsSidenavOpen,
  selectSelectedMenu,
} from './store/sidenav.selectors';
import { SidenavActions } from './store/sidenav.actions';

@Component({
  selector: 'app-sidenav',
  standalone: true,
  imports: [CommonModule, FormsModule],
  templateUrl: './sidenav.component.html',
  styleUrl: './sidenav.component.scss',
})
export class SidenavComponent {
  isOpen$ = this.store.select(selectIsSidenavOpen);
  selectedMenu$ = this.store.select(selectSelectedMenu);

  @Input() isOpen: boolean = true;
  @Output() toggleSideBar: EventEmitter<any> = new EventEmitter<any>();

  menus = MENUS;
  activeMenuId: number | null = null;
  activeSubMenuId: number | null = null;
  openSubMenuId: number | null = null;

  activeMenu: string = '';

  constructor(
    private router: Router,
    private _authService: AuthService,
    private store: Store
  ) {}

  selectMenu(menu: string) {
    this.store.dispatch(
      SidenavActions['[Sidenav]SelectMenu']({
        selectedMenu: menu,
      })
    );
  }

  onToggleSidenav() {
    this.toggleSideBar.emit();
  }

  navigate(route: string | undefined) {
    this.router.navigate([`/${route}`]);
  }

  onLogout() {
    this._authService.logout();
    this.navigate(path.LOGIN);
  }

  onClickMenu(menu: any) {
    if (!this.isOpen) {
      this.onToggleSidenav();
    }

    this.selectMenu(menu.route);

    if (this.activeMenuId === menu.id) {
      this.openSubMenuId = this.openSubMenuId === menu.id ? null : menu.id;
    } else {
      this.openSubMenuId = menu.id;
      this.activeMenuId = menu.id;
      this.activeSubMenuId = null;
    }
    this.navigate(menu.route);
  }

  onClickSubMenu(menu: any, subMenu: any) {
    this.activeSubMenuId = subMenu.id;
    this.selectMenu(subMenu.route);
    this.selectedMenu$.subscribe((x) => {
      this.activeMenu = x;
      console.log('menu ', this.activeMenu, subMenu.title);
    });

    if (subMenu.route === path.QUICK_ORDER) {
      this.router.navigate([`/${subMenu.route}`]);
    } else {
      this.router.navigate([`/${menu.route}/${subMenu.route}`]);
    }
  }
}
